

.App {

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.employees {
  margin-top: 85px;
}

.employee-wrapper {
  color: #4c4b4c;
  display: -ms-flexbox;
  display: flex;
  margin-left: 16px;
  letter-spacing: 0.03em;
  padding: 16px 0;
  border-bottom: 1px solid #d5d5d5;
}

.employee-wrapper .profile {
  -ms-flex: 0 0 120px;
      flex: 0 0 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden;
  background: #c0c0c0;
  -webkit-box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.1);
  box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.1);
}

.employee-wrapper .profile span {
  color: white;
  display: block;
  width: 100%;
  text-align: center;
  font-size: 56px;
  line-height: 88px;
}

.employee-wrapper .profile img {
  height: 120px;
  width: 120px;
  text-indent:-9999px;
}

.employee-wrapper .details {
  margin-top: 8px;
  margin-left: 12px;
}

.employee-wrapper .details h3 {
  font-size: 18px;
  margin: 0;
}

.employee-wrapper .details ul {
  margin: 8px 0;
  padding: 0;
}

.employee-wrapper li {
  display: inline;
  list-style: none;
  font-size: 12px;
  line-height: 1.3;
}

.employee-wrapper li:before {
  content: 'â€¢';
  margin-left: 6px;
  margin-right: 6px;
}

.employee-wrapper li:first-child:before {
  display: none;
}

.search-wrapper {
  background: white;
  padding: 12px;
  position: fixed;
  top: 0;
  width: 100%;
}

.search-wrapper input {
  background: #f0f0f0;
  border: none;
  border-radius: 4px;
  line-height: 1;
  padding: 14px 14px 14px 30px;
  width: 100%;
  font-size: 20px;
}



